.page-form-filter {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
}

.page-form-filter__group {
  padding: 0;
  margin: 0;
  border-right: none;
  border-bottom: none;
  border-left: none;

  @media (max-width: @mobile-width-only) {
    border-top: 1px solid @tiffany-lighter;

    &:not(:last-of-type) {
      margin-bottom: 42px;
    }
  }

  @media (min-width: @tablet-width) {
    border-top: none;
    justify-content: center;
  }
}

.page-form-filter__group--type {
  display: grid;
  grid-template-columns: 1fr;
  grid-row-gap: 42px;

  @media (min-width: @tablet-width) {
    grid-template-columns: repeat(4, 167px);
    grid-template-rows: 1fr;
    margin-bottom: 66px;
    padding: 0 50px;
  }

  @media (min-width: @desktop-width) {
    grid-template-columns: repeat(6, 175px);
  }
}

.page-form-filter__title {
  font-weight: 700;
  font-size: 17px;
  line-height: 16px;
  color: @tiffany-lighter;
  padding-right: 10px;
  grid-row: 1 / 2;

  @media (min-width: @tablet-width) {
    position: relative;
    grid-column: 1 / 2;
    grid-row: 1 / 2;
    padding: 0;
  }
}

.page-form-filter__title--type {
  @media (min-width: @tablet-width) {
    top: 22px;
  }
}

.page-form-filter__title--color {
  @media (min-width: @tablet-width) {
    top: 85px;
  }
}

.page-form-filter__title--full-name {
  @media (min-width: @tablet-width) {
    top: 56px;
  }
}

.page-form-filter__title--phone {
  @media (min-width: @tablet-width) {
    top: 33px;
  }
}

.page-form-filter__title--email {
  @media (min-width: @tablet-width) {
    grid-column: 1 / 2;
    grid-row: 2 / 3;
    top: 34px;
  }
}

.page-form-filter__title--addition {
  @media (min-width: @tablet-width) {
    top: 50px;
  }
}

.page-form-filter__control-label,
.page-form-filter__item,
.page-form-filter__user {
  font-weight: 700;
  font-size: 17px;
  line-height: 18px;
}

.page-form-filter__list {
  list-style-type: none;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: auto;
  margin: 0;

  @media (max-width: @mobile-width-only) {
    padding-right: 42px;
    padding-left: 59px;
  }
}

.page-form-filter__list--types {
  grid-row-gap: 24px;

  @media (max-width: @mobile-width-only) {
    grid-row: 2 / 3;
  }

  @media (min-width: @tablet-width) {
    grid-column: 2 / 4;
    grid-row-gap: 58px;
    padding-left: 60px;
  }
}

.page-form-filter__group--color {
  display: grid;
  grid-template-columns: 1fr;
  grid-row-gap: 42px;

  @media (min-width: @tablet-width) {
    background-color: @base-grey-color;
    grid-template-columns: repeat(4, 167px);
    grid-template-rows: 1fr;
    padding: 65px 50px 69px 50px;
    margin-bottom: 30px;
  }

  @media (min-width: @desktop-width) {
    grid-template-columns: repeat(6, 175px);
  }
}

.page-form-filter__list--color {
  grid-row-gap: 43px;

  @media (max-width: @mobile-width-only) {
    grid-row: 2 / 3;
  }

  @media (min-width: @tablet-width) {
    grid-column: 2 / 5;
    grid-template-columns: 1fr 1fr;
    padding-left: 60px;
  }

  @media (min-width: @desktop-width) {
    grid-column: 2 / 7;
    grid-template-columns: 1fr 1fr 1fr;
  }
}

.page-form-filter__group--name {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: auto;

  @media (min-width: @tablet-width) {
    grid-template-columns: repeat(4, 167px);
    grid-template-rows: 1fr 1fr 1fr;
    margin-bottom: 50px;
    padding: 0 50px;
  }

  @media (min-width: @desktop-width) {
    grid-template-columns: repeat(6, 175px);
  }
}

.page-form-filter__user {
  margin: 0;
  margin-top: 22px;
}

.page-form-filter__user label {
  @media (max-width: @mobile-width-only) {
    display: block;
  }
}

.page-form-filter__user--first-name {
  @media (min-width: @tablet-width) {
    grid-column: 2 / -1;
    grid-row: 1 / 2;
    display: flex;
    flex-direction: row;

    & label {
      align-self: center;
    }
  }
}

.page-form-filter__user--second-name {
  @media (min-width: @tablet-width) {
    grid-column: 2 / -1;
    grid-row: 2 / 3;
    display: flex;
    flex-direction: row;

    & label {
      align-self: center;
    }
  }
}

.page-form-filter__user--middle-name {
  @media (min-width: @tablet-width) {
    grid-column: 2 / -1;
    grid-row: 3 / 4;
    display: flex;
    flex-direction: row;

    & label {
      align-self: center;
    }
  }
}

.page-form-filter__group--contacts {
  border: none;
  margin-bottom: 30px;

  @media (min-width: @tablet-width) {
    margin-bottom: 50px;
    grid-row-gap: 24px;
    background-color: @base-grey-color;
    padding: 50px;
  }
}

.page-form-filter__group--phone {
  display: grid;

  @media (min-width: @tablet-width) {
    background-color: @base-grey-color;
    grid-template-columns: repeat(4, 167px);
  }

  @media (min-width: @desktop-width) {
    grid-template-columns: repeat(6, 175px);
  }
}

.page-form-filter__group--email {
  display: grid;

  @media (min-width: @tablet-width) {
    background-color: @base-grey-color;
    grid-template-columns: repeat(4, 167px);
  }

  @media (min-width: @desktop-width) {
    grid-template-columns: repeat(6, 175px);
  }
}

.page-form-filter__group--addition {
  display: grid;

  @media (min-width: @tablet-width) {
    grid-template-columns: repeat(4, 167px);
    grid-template-rows: auto;
    padding: 0 50px;
  }

  @media (min-width: @desktop-width) {
    grid-template-columns: repeat(6, 175px);
  }
}

.page-form-filter__addition {
  & textarea {
    resize: none;
  }

  @media (min-width: @tablet-width) {
    grid-column: 2 / 5;
    grid-row: 1 / 2;
  }

  @media (min-width: @desktop-width) {
    grid-column: 2 / -1;
  }
}

.page-form-filter__item {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
}

.page-form-filter__control {
  position: relative;
}

.page-form-filter__button {
  font-family: "Open Sans", "Arial", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 17px;
  line-height: 16px;
  padding: 16px 37px;
  margin-top: 30px;
  color: @darken-blue-black;

  @media (min-width: @tablet-width) {
    grid-column: 1 / 3;
    grid-row: 2 / 3;
  }

  @media (min-width: @desktop-width) {
    grid-column: 2 / 4;
  }
}

.page-form-filter__hint {
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
  opacity: 0.3;
  margin: 0;

  @media (min-width: @tablet-width) {
    grid-column: -1 / -3;
    grid-row: 2 / 3;
    justify-self: end;
    position: relative;
    top: 47px;
  }
}

.page-form-filter__control-mark {
  box-sizing: border-box;
  position: absolute;
  width: 42px;
  height: 42px;
  left: -61px;
  top: -8px;
}

/*RADIO-BUTTONS*/
.page-form-filter__control-input[type="radio"]:checked + .page-form-filter__control-mark::before {
  content: url("../img/icon-radio-on.svg");
}

.page-form-filter__control-input[type="radio"]:not(:checked) + .page-form-filter__control-mark::before {
  content: url("../img/icon-radio-off.svg");
}

.page-form-filter__control-input[type="radio"]:checked:hover + .page-form-filter__control-mark::before {
  content: url("../img/icon-radio-on-hover.svg");
}

.page-form-filter__control-input[type="radio"]:not(:checked):hover + .page-form-filter__control-mark::before {
  content: url("../img/icon-radio-off-hover.svg");
}

.page-form-filter__control-input[type="radio"]:checked:active + .page-form-filter__control-mark::before {
  content: url("../img/icon-radio-on-down.svg");
}

.page-form-filter__control-input[type="radio"]:not(:checked):active + .page-form-filter__control-mark::before {
  content: url("../img/icon-radio-off-down.svg");
}

/*CHECKBOX-BUTTONS*/
.page-form-filter__control-input[type="checkbox"]:checked + .page-form-filter__control-mark::before {
  content: url("../img/icon-check-on.svg");
}

.page-form-filter__control-input[type="checkbox"]:not(:checked) + .page-form-filter__control-mark::before {
  content: url("../img/icon-check-off.svg");
}

.page-form-filter__control-input[type="checkbox"]:checked:hover + .page-form-filter__control-mark::before {
  content: url("../img/icon-check-on-hover.svg");
}

.page-form-filter__control-input[type="checkbox"]:not(:checked):hover + .page-form-filter__control-mark::before {
  content: url("../img/icon-check-off-hover.svg");
}

.page-form-filter__control-input[type="checkbox"]:checked:active + .page-form-filter__control-mark::before {
  content: url("../img/icon-check-on-down.svg");
}

.page-form-filter__control-input[type="checkbox"]:not(:checked):active + .page-form-filter__control-mark::before {
  content: url("../img/icon-check-off-down.svg");
}

.page-form-filter__text-input {
  outline: 0;
  outline-offset: 0;
  border: none;
  border-bottom: 2px solid @font-regular;
  padding: 17px 10px 17px 0;
  font-weight: 400;
  font-size: 19px;
  line-height: 18px;

  @media (max-width: @mobile-width-only) {
    width: 100%;
  }

  @media (min-width: @tablet-width) {
    &:hover,
    &:hover:active,
    &:focus {
      border-bottom: 2px solid @tiffany-link;
    }
  }
}

.page-form-filter__text-input--name {
  @media (min-width: @tablet-width) {
    margin-left: 24px;
    flex-grow: 1;
  }
}

.page-form-filter__text-input::placeholder,
.page-form-filter__wide::placeholder {
  font-weight: 400;
  font-size: 19px;
  line-height: 18px;
  opacity: 0.3;
}

.page-form-filter__phone,
.page-form-filter__email {
  position: relative;
  margin: 0;

  @media (min-width: @tablet-width) {
    display: flex;
    flex-direction: column;
    grid-column: 2 / -1;
  }
}

.page-form-filter__text-input--phone,
.page-form-filter__text-input--email {
  @media (min-width: @tablet-width) {
    background-color: @base-grey-color;
  }
}

.page-form__icon--phone,
.page-form__icon--email {
  position: absolute;
  box-sizing: border-box;
  top: 12px;
  right: 0;
}

.page-form__icon--phone::after {
  content: url("../img/icon-phone.svg");
}

.page-form__icon--email::after {
  content: url("../img/icon-letter.svg");
}

.page-form-filter__text-input--phone:hover + .page-form__icon--phone::after,
.page-form-filter__text-input--phone:active + .page-form__icon--phone::after,
.page-form-filter__text-input--phone:focus + .page-form__icon--phone::after {
  content: url("../img/icon-phone-hover.svg");
}

.page-form-filter__text-input--email:hover + .page-form__icon--email::after,
.page-form-filter__text-input--email:active + .page-form__icon--email::after,
.page-form-filter__text-input--email:focus + .page-form__icon--email::after {
  content: url("../img/icon-letter-hover.svg");
}

.page-form-filter__wide {
  outline: 0;
  outline-offset: 0;
  font-weight: 400;
  font-size: 19px;
  line-height: 18px;
  margin-top: 10px;
  box-sizing: border-box;
  border: 2px solid @darken-blue-black;
  width: 100%;
  padding: 17px;
  min-height: 142px;

  &:hover,
  &:hover:active,
  &:focus {
    border: 2px solid @tiffany-link;
  }
}
