.contacts {
  background-color: @base-bg-color;
  font-family: "Open Sans", "Arial", sans-serif;
  font-style: normal;
  color: @font-regular;

  @media (max-width: @mobile-width-only) {
    min-height: 818px;
    margin-top: 38px;
  }

  @media (min-width: @tablet-width) {
    min-height: 824px;
  }

  @media (min-width: @desktop-width) {
    min-height: 457px;
    margin-bottom: 105px;
  }
}

.contacts__wrapper {
  @media (max-width: @mobile-width-only) {
    min-height: 818px;
    margin-top: 38px;
  }

  @media (min-width: @tablet-width) {
    margin-top: 60px;
    padding-left: 50px;
    padding-right: 50px;
  }

  @media (min-width: @desktop-width) {
    min-height: 457px;
    display: grid;
    grid-template-columns: 175px 350px 525px;
    grid-template-rows: 95px 80px 228px 52px;
  }
}

.contacts__title {
  font-weight: 700;
  font-size: 25px;
  line-height: 30px;
  margin: 0;

  @media (min-width: @tablet-width) {
    font-size: 43px;
    line-height: 50px;
    position: relative;
  }

  @media (min-width: @desktop-width) {
    grid-column: 2 /3;
    grid-row: 2 / 3;
    padding: 0;
  }
}

.contacts__title::after {
  @media (min-width: @tablet-width) {
    content: url("../img/flag.svg");
    width: 47px;
    height: 81px;
    position: absolute;
    right: 0;
    top: -20px;
  }

  @media (min-width: @desktop-width) {
    left: -110px;
    top: -20px;
  }
}

.contacts__list {
  font-weight: 400;
  font-size: 14px;
  line-height: 30px;
  display: grid;
  padding-left: 30px;
  padding-right: 30px;

  @media (max-width: @mobile-width-only) {
    grid-template-columns: 1fr;
    grid-template-rows: 1fr;
  }

  @media (min-width: @tablet-width) {
    grid-template-columns: 1fr 1fr;
    margin-top: 60px;
  }

  @media (min-width: @desktop-width) {
    grid-column: 2 / 3;
    grid-row: 3 / 4;
    grid-template-columns: 1fr;
    grid-template-rows: 50px 100px;
    margin: 0;
    padding: 0;
  }
}

.contacts__item {
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 3fr;
}

.contacts__item:not(:last-child) {
  @media (max-width: @mobile-width-only) {
    margin-bottom: 30px;
  }
}

.contacts__parameter,
.contacts__description {
  color: @darken-blue-black;
  margin: 0;
}

.contacts__description a {
  text-decoration: none;
  color: @darken-blue-black;
}

a[href^="info@mimimishkashop.ru"] {
  padding-bottom: 5px;
  border-bottom: 1px solid @tiffany-link;

  &:hover {
    color: @tiffany-link;
  }

  &:hover:active {
    color: @tiffany-link;
    border-bottom: none;
  }
}

.contacts__map {
  background-repeat: no-repeat;
  display: flex;
  justify-content: center;

  @media (max-width: @mobile-width-only) {
    margin-bottom: 42px;
  }

  @media (min-width: @tablet-width) {
    position: relative;
    margin-top: 60px;
    margin-bottom: 50px;
  }

  @media (min-width: @desktop-width) {
    grid-column: 3 / -1;
    grid-row: 1 / 5;
    margin: 0;
  }
}

.contacts__link {
  display: block;
  text-decoration: none;
  font-weight: 700;
  font-size: 18px;
  line-height: 20px;
  color: @darken-blue-black;
  text-align: center;
  padding: 16px 40px;
  margin: 0 30px;

  @media (min-width: @tablet-width) {
    padding: 16px 28px;
    display: inline-block;
    position: relative;
    left: 33%;
    width: 160px;
    margin: 0 auto;
  }

  @media (min-width: @desktop-width) {
    grid-column: 2 / 3;
    grid-row: 4 / 5;
    margin: 0;
    position: static;
  }
}
