.main-nav__toggle {
  position: absolute;
  top: -79px;
  right: 0;
  width: 80px;
  height: 80px;
  border: none;
  background-color: transparent;
  cursor: pointer;
  z-index: 2;
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}
/* Здесь тоггл в открытом состоянии (позиционирование отличается от
настроек .main-nav__toggle выше, поэтому top продублирован)*/
.main-nav--opened .main-nav__toggle {
  top: -79px;
  right: 0;
}
.main-nav--opened .main-nav__toggle::before,
.main-nav--opened .main-nav__toggle::after {
  content: "";
  position: absolute;
  top: 40px;
  left: 30px;
  width: 30px;
  height: 2px;
  background-color: #231f20;
}
.main-nav--opened .main-nav__toggle::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.main-nav--opened .main-nav__toggle::after {
  transform: rotate(-45deg);
}
.main-nav--opened .main-nav__toggle:active::before,
.main-nav--opened .main-nav__toggle:active::after {
  background-color: rgba(255, 255, 255, 0.3);
}
.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 32px;
  left: 31px;
  width: 20px;
  height: 2px;
  background-color: #231f20;
  box-shadow: 0 8px 0 0 #231f20, 0 16px 0 0 #231f20;
}
.main-nav--closed .main-nav__toggle:active::before {
  background-color: rgba(35, 31, 32, 0.3);
  box-shadow: 0 8px 0 0 rgba(35, 31, 32, 0.3), 0 16px 0 0 rgba(35, 31, 32, 0.3);
}
