.page-form-filter__text-input--email:active + .page-form__icon--email::after,
.page-form-filter__text-input--email:focus + .page-form__icon--email::after {
  content: url("../img/icon-letter-hover.svg");
}
.page-form-filter__wide {
  outline: 0;
  outline-offset: 0;
  font-weight: 400;
  font-size: 19px;
  line-height: 18px;
  margin-top: 10px;
  box-sizing: border-box;
  border: 2px solid #444444;
  width: 100%;
  padding: 17px;
  min-height: 142px;
}
.page-form-filter__wide:hover,
.page-form-filter__wide:hover:active,
.page-form-filter__wide:focus {
  border: 2px solid #62d1ba;
}
.order-form {
  font-family: "Open Sans", "Arial", sans-serif;
  font-style: normal;
  color: #000000;
  margin-bottom: 55px;
  position: relative;
}
.order-form__wrapper {
  padding-left: 30px;
  padding-right: 30px;
}
@media (min-width: 768px) {
  .order-form__wrapper {
    padding: 0;
  }
}
.order-form__text {
  font-weight: 400;
  font-size: 13px;
  line-height: 24px;
  margin-bottom: 37px;
  color: #444444;
  padding: 0 50px;
}
@media (min-width: 1150px) {
  .order-form__break-tablet {
    display: none;
  }
}
@media (max-width: 768px) {
  .order-form__break-desktop {
    display: none;
  }
}
@media (min-width: 1150px) {
  .order-form__break-desktop {
    display: inline;
  }
}
